import React, { useEffect, useState } from 'react'
import Layout from '../../Layout/Layout'
import AdminMenu from '../Admincontrol/AdminMenu'
import { toast } from 'react-toastify';
import axios from 'axios';

const User = () => {

    const [users, setUsers] = useState([]);
    const [userid, setUserId] = useState('');
    const [role, setRole] = useState('');
    const getUsers = async () => {
        try {
            const { data } = await axios.get("/api/v1/admin/get-users")
            console.log(data)
            if (data?.success) {
                setUsers(data.users)
            }
        } catch (error) {
            console.log(error)

        }
    }
    useEffect(() => { getUsers() }, [])

    const handleRole = async (user) => {
        setUserId(user._id);
        // Use the state callback function to ensure updated state is used
        setRole((prevRole) => (user.role === "user" ? "admin" : "user"));
        handleUpdateRole();
    };

    // ... your existing code ...

    const handleUpdateRole = async () => {
        try {
            const { data } = await axios.put(
                `api/v1/admin/user-profile-role/${userid}`,
                { role: role }, // Use the updated role from state
                { new: true }
            );
            if (data?.success && data.status === 201) {
                toast.success("User role updated successfully");
                // After successful update, refresh the users list to show the updated roles
                getUsers();
            } else {
                toast.error("User role update unsuccessful");
            }
        } catch (error) {
            console.log(error);
        }
    };

    return (
        <>
            <Layout>
                <div className='contact_section'>
                    <div className="col-sm-12">
                        <h1 className="contact_taital">User Monitor Pannel</h1>
                        <div className="bulit_icon"><img src="Assets/images/bulit-icon.png" /></div>
                    </div>
                    <div className='container-fluid' m-3 p-3>
                        <div className='row'>
                            <div className='col-md-3 mt-4 mb-5'>
                                <AdminMenu />
                            </div>
                            <div className='col-md-9'><div className='card w-40 mt-5 p-3'>
                                <h1>User Control</h1>
                                <div className='w-75'>
                                    <div className='table-responsive'>
                                        <table className="table">
                                            <thead>
                                                <tr>
                                                    <th scope="col">Name</th>
                                                    <th scope="col">Email</th>
                                                    <th scope="col">Role</th>
                                                </tr>

                                            </thead>
                                            <tbody>
                                                {users?.map((c) => (
                                                    <>
                                                        <tr>
                                                            <td key={c._id}>{c.name}</td>
                                                            <td key={c._id}>{c.email}</td>
                                                            <td key={c._id}>{c.role}</td>
                                                            {(c.role === "user") ? (
                                                                <td><button className='btn btn-danger ms-2' onClick={() => { handleRole(c) }}>Make Admin</button></td>
                                                            ) : (

                                                                <td><button className='btn btn-danger ms-2' onClick={() => { handleRole(c) }}>Make User</button></td>
                                                            )}
                                                        </tr>
                                                    </>
                                                ))}
                                            </tbody>
                                        </table>
                                    </div>
                                </div>
                            </div>
                            </div>
                        </div>
                    </div>
                </div>
            </Layout>

        </>

    )
}

export default User
